import {AxiosError, AxiosResponse} from "axios";


const messageErrors = new Set();
messageErrors.add(400);
messageErrors.add(422);
messageErrors.add(404);
messageErrors.add(500);
messageErrors.add(403);

const defaultHandler = (response: AxiosResponse | undefined) => {
  return `Erro Desconhecido: ${response?.status}`
}

function handleAxiosError(error: AxiosError): string {

  if (error.isAxiosError && error.response === undefined) {
    return 'Ops não foi possível conectar ao servidor';
  }

  const response = error.response as AxiosResponse;
  if (messageErrors.has(response?.status)) {
    return response.data?.message;
  }

  return defaultHandler(response);
}

export default handleAxiosError
