import axios from 'axios';
import {Auth} from "../domain/auth/Auth";

const vouDeVanApi = axios.create({
  baseURL: 'http://ec2-18-230-152-119.sa-east-1.compute.amazonaws.com:8080/admin',
  headers: {
    'Access-Control-Allow-Origin': '*',
  }
});

vouDeVanApi.interceptors.request.use(function (config) {
  if (!Auth.instance.isAuthenticated) return config;

  config.headers.Authorization = `Bearer ${Auth.instance.token}`

  return config;
});

export default vouDeVanApi;


