import React, {lazy, Suspense} from 'react';
import {HashRouter, Redirect, Route, Switch} from 'react-router-dom';

import './scss/App.scss';
import AppPageLoading from './components/AppPageLoading';
import Login from "./pages/Login";
import {RouteProps} from "react-router";
import {Auth} from "./domain/auth/Auth";


const DefaultLayout = lazy(() => import('./containers/DefaultLayout'));

const App: React.FC = () => (
  <HashRouter>
    <Suspense fallback={AppPageLoading}>
      <Switch>
        <Route exact path="/login" render={props => <Login {...props}/>}/>
        <AuthRoute path="/" render={props => <DefaultLayout {...props}/>}/>
      </Switch>
    </Suspense>
  </HashRouter>
)


function AuthRoute(props: RouteProps) {

  if (Auth.instance.isAuthenticated) {
    return <Route {...props}/>
  }


  return <Route
    {...props}
    render={({location}) => <Redirect to={{pathname: "/login", state: {from: location}}}/>}
  />


}


export default App;
