import AlertModel from "../common/AlertModel";
import {UncontrolledAlert} from "reactstrap";
import React from "react";


export default function AppAlert({alertModel}: { alertModel?: AlertModel }) {


  return<>
    {
      alertModel ? <UncontrolledAlert color={alertModel.color}>{alertModel.message}</UncontrolledAlert> : null
    }
  </>
}
