import vouDeVanApi from "../../common/vouDeVanApi";
import {Auth} from "./Auth";

export interface LoginFormModel {
  email: String,
  password: String
}

export function postLogin(loginFormModel: LoginFormModel) {

  return vouDeVanApi.post<string>('/login', loginFormModel)
    .then(response => {
      Auth.instance.token = response.data;
    })
}
