import React from 'react';
import {Button, Card, CardBody, CardGroup, Col, Container, Form, Row} from 'reactstrap';
import {FormikHelpers} from "formik/dist/types";

import {Field, Formik} from "formik";

import AppInputGroup from "../../components/AppInputGroup";
import {LoginFormModel} from "../../domain/auth/Login";
import AlertModel from "../../common/AlertModel";
import AppAlert from "../../components/AppAlert";


interface LoginViewModel extends LoginFormViewModel {
  alertModel?: AlertModel
}

export function LoginView({alertModel, ...props}: LoginViewModel) {

  return (
    <div className="app flex-row align-items-center">
      <Container>

        <Row className="justify-content-center">
          <Col md="4">
            <CardGroup>
              <Card className="p-4">
                <CardBody>
                  <AppAlert alertModel={alertModel}/>
                  <LoginFormView {...props}/>
                </CardBody>
              </Card>
            </CardGroup>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

interface LoginFormViewModel {
  initialValues: LoginFormModel
  onSubmit: (values: LoginFormModel, formikHelpers: FormikHelpers<LoginFormModel>) => void | Promise<LoginFormModel>;
  validationSchema: any,

}


function LoginFormView({initialValues, validationSchema, onSubmit}: LoginFormViewModel) {
  return (<Formik
    initialValues={initialValues}
    validationSchema={validationSchema}
    onSubmit={onSubmit}
  >
    {({handleSubmit, isSubmitting}) => {
      return (
        <Form onSubmit={handleSubmit}>

          <h1>Login</h1>
          <p className="text-muted">Entrar na sua conta</p>

          <Field
            type="email"
            name="email"
            id="email"
            maxLength={140}
            icon={"icon-user"}
            component={AppInputGroup}
          />

          <Field
            type="password"
            label="Senha"
            name="password"
            id="password"
            maxLength={140}
            icon={"icon-lock"}
            component={AppInputGroup}
          />

          <Row>
            <Col xs="6">
              <Button type={'submit'} color="primary" className="px-4" disabled={isSubmitting}>Login</Button>
            </Col>
            <Col xs="6" className="text-right">
              <Button color="link" className="px-0">Forgot password?</Button>
            </Col>
          </Row>
        </Form>
      );
    }}
  </Formik>);
}


