import AppInput, {AppInputModel} from "./AppInput";
import {InputGroup, InputGroupAddon, InputGroupText} from "reactstrap";
import React from "react";

export interface FormInputGroupModel extends AppInputModel {
  icon: string
}

export default function AppInputGroup({icon, ...props}: FormInputGroupModel) {

  return (
    <>
      <InputGroup className="mb-3">
        <InputGroupAddon addonType="prepend">
          <InputGroupText>
            <i className={icon}/>
          </InputGroupText>
        </InputGroupAddon>
        <AppInput {...props}/>
      </InputGroup>
    </>);
}
