import {FormFeedback, Input, InputProps} from "reactstrap";
import React from "react";
import {useField} from "formik";
import InputMask from "react-input-mask";

export interface AppInputModel extends InputProps {
  id: string,
  mask?: string | Array<(string | RegExp)>
}


export default function AppInput({id, mask, ...props}: AppInputModel) {

  const [field, meta] = useField(id);
  const hasError = (): boolean => {
    return !!(meta.touched && meta.error);
  }

  if (mask) {
    return <> <InputMask mask={mask}  {...props} {...field}  >
      {(inputProps: object) => {
        return <Input id={id} {...inputProps} invalid={hasError()}/>
      }}
    </InputMask>
      {hasError() ? <FormFeedback>{meta.error}</FormFeedback> : ''}
    </>
  }


  return (<>
    <Input id={id} {...field} invalid={hasError()} {...props}/>
    {hasError() ? <FormFeedback>{meta.error}</FormFeedback> : ''}
  </>);

}

