import React, {useState} from 'react';
import {LoginView} from "./LoginView";
import Yup from "../../common/Yup";

import {FormikHelpers} from "formik/dist/types";
import {RouteComponentProps} from "react-router";
import {LoginFormModel, postLogin} from "../../domain/auth/Login";
import AlertModel, {alertFromAxiosError} from "../../common/AlertModel";

const validationSchema = Yup.object().shape({
  email: Yup.string().max(150).email().required(),
  password: Yup.string().max(150).required(),
});


export default function Login({history, location}: RouteComponentProps) {

  const [alertModel, setAlertModel] = useState<AlertModel | undefined>(undefined);


  const initialValues: LoginFormModel = {email: "", password: ""}
  const onSubmit = (loginFormModel: LoginFormModel, {setSubmitting}: FormikHelpers<LoginFormModel>) => {
    setAlertModel(undefined);

    postLogin(loginFormModel)
      .then(() => {
        const state: any = location.state;
        const {from} = state || {from: {pathname: "/"}};
        history.replace(from);
      })
      .catch(e => {
        setAlertModel(alertFromAxiosError(e))
        setSubmitting(false)
      });
  }

  return (<LoginView
    {...{
      validationSchema,
      initialValues,
      onSubmit,
      alertModel
    }}
  />);
}
